/* ì „ì²´ ì»¨í…Œì´ë„ˆ */
.writePageContainer {
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  background: #fff;
  min-height: 100vh;
}
.label{
  margin: auto 0;
}

/* í—¤ë” ì˜ì—­ */
.writeHeader {
  margin-bottom: 12px;
  padding-bottom: 12px;
}

.writeTitle {
  font-size: 14px;
  color: #6b7280;
  font-weight: 400;
  margin: 0;
  letter-spacing: 0.5px;
}

/* í¼ ìŠ¤íƒ€ì¼ */
.form {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

/* ë¼ë²¨ */
.label {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: #374151;
  margin-bottom: 4px;
}

/* ======================================================= */
/* âœ… [í•µì‹¬] ì œëª©ê³¼ ì¸ë„¤ì¼ì„ ê°ì‹¸ëŠ” ê·¸ë£¹ (Flexbox 50:50) */
/* ======================================================= */
.titleThumbnailGroup {
  display: flex;
  flex-direction: row; /* ê°€ë¡œ ë°°ì—´ */
  gap: 15px; /* ì œëª©ê³¼ ì¸ë„¤ì¼ ì‚¬ì´ ê°„ê²© */
}

/* ì œëª© ì…ë ¥ í•„ë“œë¥¼ ê°ì‹¸ëŠ” Wrapper (50% ë¹„ìœ¨) */
.titleInputWrapper {
  margin-top:20px;
  /* gap 30px ì¤‘ ì ˆë°˜(15px)ì„ ì œì™¸í•œ 50% */
  flex-basis: calc(50% - 15px);
  flex-shrink: 0;
}

/* ì œëª© ì…ë ¥ */
.titleInput {
  width: 100%;
  padding: 4px;
  font-size: 24px;
  font-weight: 500;
  border: none;
  outline: none;
  transition: border-color 0.3s;
  background: transparent;

}

.titleInput:focus {
  border-bottom-color: #3b82f6;
}

.titleInput::placeholder {
  color: #d1d5db;
  font-weight: 400;
}
/* ======================================================= */

/* ì¼ë°˜ í…ìŠ¤íŠ¸ ì…ë ¥ */
.textInput {
  width: 100%;
  padding: 8px;
  font-size: 14px;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  outline: none;
  transition: all 0.3s;
}

.textInput:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

/* íŒíŠ¸ í…ìŠ¤íŠ¸ */
.hint {
  display: block;
  margin-top: 6px;
  font-size: 12px;
  color: #9ca3af;
}

/* ì²´í¬ë°•ìŠ¤ ì˜ì—­ */
.checkboxWrapper {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px;
  background: #f9fafb;
  border-radius: 6px;
}

.checkbox {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.checkboxLabel {
  font-size: 14px;
  color: #374151;
  cursor: pointer;
  user-select: none;
}

/* ì œì¶œ ë²„íŠ¼ */
.submitButton {
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  background: #3b82f6;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
  align-self: flex-start;
}

.submitButton:hover:not(:disabled) {
  background: #2563eb;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.submitButton:disabled {
  background: #9ca3af;
  cursor: not-allowed;
  transform: none;
}

/* ======================================================= */
/* ì¸ë„¤ì¼ ê´€ë ¨ ìŠ¤íƒ€ì¼ (50% ë„ˆë¹„, ë†’ì´ 120px) */
/* ======================================================= */

.thumbnailSection {
  /* gap 30px ì¤‘ ì ˆë°˜(15px)ì„ ì œì™¸í•œ 50% */
  flex-basis: calc(50% - 15px);
  flex-shrink: 0;

  /* ê¸°ì¡´ ìŠ¤íƒ€ì¼ ìœ ì§€ */
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 0;
}

.thumbnailSection h3 {
  font-size: 16px;
  margin-bottom: 12px;
  color: #374151;
}

.thumbnailWrapper {
  min-height: 120px;
  background-color: #f9f9f9;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  cursor: pointer;
}

/* ì—…ë¡œë“œ ì¤‘ì¼ ë•Œ ì»¤ì„œ ë³€ê²½ */
.thumbnailWrapper[disabled] {
  cursor: not-allowed;
}

.thumbnailPreview {
  max-width: 100%;
  max-height: 120px;
  width: auto;
  object-fit: contain;
}

/* ì¸ë„¤ì¼ ì œê±° ë²„íŠ¼ */
.removeThumbnailBtn {
  margin-top: 10px;
  padding: 5px 10px;
  border: 1px solid #dc3545;
  background-color: #fff;
  color: #dc3545;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.removeThumbnailBtn:hover {
  background-color: #ffeaea;
}

/* ======================================================= */
/* íƒœê·¸ ì…ë ¥ ê·¸ë£¹ ìŠ¤íƒ€ì¼ */
.tagInputGroup {
  margin-bottom: 0;
}
.tagList {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 8px;
}
.tagItem {
  background: #e5e7eb;
  color: #4b5563;
  padding: 4px 8px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
}
.removeTagBtn {
  border: none;
  background: none;
  cursor: pointer;
  margin-left: 4px;
  color: #4b5563;
  font-weight: bold;
  font-size: 14px;
  padding: 0 2px;
}


/* ======================================================= */
/* ë°˜ì‘í˜• - íƒœë¸”ë¦¿ ì´í•˜ (1024px ì´í•˜ì—ì„œëŠ” 1ì—´ë¡œ ë³€ê²½) */
/* ======================================================= */
@media (max-width: 1024px) {
  /* ì œëª© + ì¸ë„¤ì¼ ê·¸ë£¹ì„ ìˆ˜ì§(column)ìœ¼ë¡œ ë³€ê²½ */
  .titleThumbnailGroup {
    flex-direction: column;
    gap: 20px;
  }

  .writePageContainer {
    padding: 15px;
  }

  /* âœ… ìˆ˜ì •: 1024px ì´í•˜ì—ì„œëŠ” 100% ë„ˆë¹„ë¡œ ì¬ì„¤ì • */
  .titleInputWrapper {
    flex-basis: auto;
    width: 100%;
  }
  .thumbnailSection {
    flex-basis: auto;
    width: 100%;
  }

  .titleInput {
    font-size: 20px;
  }
}

@media (max-width: 768px) {
  .writePageContainer {
    padding: 12px;
  }

  .titleInput {
    font-size: 18px;
    padding: 12px;
  }

  .form {
    gap: 20px;
  }
}




