/* TiptapEditor.module.css */

.editorWrapper {

  display: flex;
  gap: 0rem;
  height: 600px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  overflow: hidden;
}

.editorPane {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: #ffffff;
}

.menuBar {

  display: flex;
  gap: 0.25rem;
  padding: 0.75rem;
  background-color: #f9fafb; /* ìƒë‹¨ ë©”ë‰´ë°” ë°°ê²½ìƒ‰ */
  border-bottom: 1px solid #e5e7eb;
  flex-wrap: wrap;
}

.menuBar button {
  padding: 0.5rem 0.75rem;
  border: 1px solid #d1d5db;
  background-color: #ffffff;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.875rem;
  transition: all 0.2s;
}

.menuBar button:hover {
  background-color: #f3f4f6;
}

.menuBar button.isActive {
  background-color: #6366f1;
  color: #ffffff;
  border-color: #6366f1;
}

.editorContent {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
}

/* âœ… ProseMirror ì—ë””í„° ì˜ì—­ì— white-space ì„¤ì • ì¶”ê°€ */
.editorContent :global(.ProseMirror) {
  min-height: 100%;
  outline: none;
  white-space: pre-wrap; /* ê³µë°±ê³¼ ì¤„ë°”ê¿ˆ ë³´ì¡´ */
  word-wrap: break-word;
}

.editorContent :global(.ProseMirror) p {
  margin: 0.5rem 0;
  white-space: pre-wrap; /* ë¬¸ë‹¨ ë‚´ ê³µë°± ë³´ì¡´ */
  min-height: 1.5em; /* âœ… ë¹ˆ ë¬¸ë‹¨ë„ ë†’ì´ í™•ë³´ */
  line-height: 1.8; /* âœ… ì¤„ ë†’ì´ ëª…ì‹œ - ë¯¸ë¦¬ë³´ê¸°ì™€ ë™ì¼ */
}

/* âœ… ë¹ˆ ë¬¸ë‹¨ì— ìµœì†Œ ë†’ì´ ë¶€ì—¬ */
.editorContent :global(.ProseMirror) p:empty::before {
  content: '';
  display: block;
  min-height: 1.5em;
}

.editorContent :global(.ProseMirror) h1,
.editorContent :global(.ProseMirror) h2,
