.imageResizeModal {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modalContent {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  width: 90%;
  max-width: 600px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.3);
}

.resizeControl {
  margin: 1.5rem 0;
}

.resizeControl input[type="range"] {
  width: 100%;
  height: 8px;
  border-radius: 4px;
  background: #ddd;
  outline: none;
}

.resizeControl input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #4CAF50;
  cursor: pointer;
}

.modalButtons {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.applyBtn, .cancelBtn {
  flex: 1;
  padding: 0.8rem;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
}

.applyBtn { background: #4CAF50; color: white; }
.cancelBtn { background: #f44336; color: white;color: white; }

.previewContent img {
  display: block;
  height: auto;
}

