.alignControl {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.alignButtons {
  display: flex;
  gap: 10px;
}

.alignButtons button {
  flex: 1;
  padding: 0.6rem;
  border: 1px solid #d1d5db;
  background-color: #ffffff;
  border-radius: 4px;
  cursor: pointer;
}

.alignButtons button:hover {
  background-color: #f3f4f6;
}

.alignActive {
  background-color: #6366f1 !important;
  color: #ffffff !important;
  border-color: #6366f1 !important;
}